import React from 'react';

import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { selectColors } from '../../redux/themes/themes.selectors';

import {
  ProjectItemContainer,
  Thumbnail,
  Details,
  Title,
  Description,
  Tags,
  Tag,
} from './project-item.styles';

import coronaTodayImage from '../../images/corona-today.png';
import qurankuImage from '../../images/quranku.png';
import urbanImage from '../../images/urban-clothing.png';
import myLeagueImage from '../../images/my-league.png';
import cariRestoImage from '../../images/cari-resto.png';

const ProjectItem = ({ colors, data }) => {
  const { bg1, bg2, bg3, font1, font4, shadow1, shadow2 } = colors;

  switch(data) {
    case undefined:
      return null;
    default:
      const { title, description, url, imageName, tags } = data;
      let imgSrc = null;

      switch(imageName) {
        case 'quranku.png':
          imgSrc = qurankuImage;
          break;
        case 'urban-clothing.png':
          imgSrc = urbanImage;
          break;
        case 'corona-today.png':
          imgSrc= coronaTodayImage;
          break;
        case 'my-league.png':
          imgSrc = myLeagueImage;
          break;
        case 'cari-resto.png':
          imgSrc = cariRestoImage;
          break;
        default:
          break;
      }

      return (
        <ProjectItemContainer
          bg={bg1}
          shadow1={shadow1}
          shadow2={shadow2}
          href={url}
          rel="noreferrer"
          target="_blank"
        >
          <Thumbnail src={imgSrc} alt={title} bg={bg3} />
          <Details>
          <Title>{title}</Title>
          <Description font={font1}>{description}</Description>
            <Tags>
              {
                tags.map((tag, index) => (
                  <Tag key={index} bg={bg2} font={font4}>
                    {tag}
                  </Tag>
                ))
              }
            </Tags>
          </Details>
        </ProjectItemContainer>
      );
  }
};

const mapStateToProps = createStructuredSelector({
  colors: selectColors,
});

export default connect(mapStateToProps)(ProjectItem);
